body,html,#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
.App {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-flow: column nowrap;
  align-items: center;
  background-color: #3d3b3bd8;
}

header{
  height: 100px;
  width: 100%;
}
main{
  width: calc(100% - 100px);
  height: calc(100% - 150px);
  background: #ffffff;
  border-radius: 3px;
  box-shadow: 0px 2px 10px 10px #00000040;
  position: relative;
  padding: 15px;
}
footer{
  height: 50px;
  width: 100%;
}

.to__do__list{
  width: 100%;
  height: 100%;
  position: relative;
  overflow: auto;
}