.to__do__list__adding{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}
.to__do__list__adding input {
    border: 2px solid #636dfa;
    margin: 0 6px;
    padding: 4px;
    border-radius: 3px;
    width: 50%;
}
.list{
    display: flex;
    flex-flow: row wrap;
    justify-content: start;
    align-items: center;
    padding: 5px;
}
.list span {
    padding: 3px;
}
.list .list__btn__action{
    margin-left: auto;
}
.change__content{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    top:0;
    left:0;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #dadada;
    z-index: 15;
    border-radius: 3px;
}
.change__content-container{
    padding: 30px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    background-color: white;
}
.change__content-container textarea{
    min-width: 200px;
    min-height: 200px;
}
.change__content-container__btns{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-top:15px;
}
.isChecking-true input,
.isChecking-false input{
    opacity: 0;
    z-index: 10;
    position: relative;
    padding: 0px;
    margin: 0px;
    width: 100%;
    height: 100%;
}
.isChecking-true,
.isChecking-false {
    width: 15px;
    height: 15px;
    position: relative;
    margin: 0 5px;
}
.isChecking__span{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    left: 0;
    padding: 0px!important;
    margin: 0;
    z-index: 5;
    border: none;
    background-color: teal;
    border-radius: 3px;
}

.isChecking-true .isChecking__span:nth-of-type(1){
    width: 33%;
    left: 0;
    animation: shadow  ease-in-out infinite;
    animation-duration: 1.5s;
    background-color: teal;
}
.isChecking-true .isChecking__span:nth-of-type(2){
    
    width: 33%;
    left:33%;
    animation: shadow  ease-in-out infinite;
    animation-duration: 1.5s;
    animation-delay: .5s;
    background-color: teal;

}
.isChecking-true .isChecking__span:nth-of-type(3){
    width: 33%;
    left:66%;
    animation: shadow ease-in-out infinite;
    animation-duration: 1.5s;
    animation-delay: .8s;
    background-color: teal;
}
@keyframes shadow{
    from{
        opacity: 0;
        height: 0%;
        top:50%;
        border: 1px solid white;
    }
    to{
        opacity: 1;
        height: 100%;
        border: 1px solid transparent;
    }
}
.list-true {
    text-decoration:line-through wavy 2px #bff5e7;
    border-radius: 3px;
    color: rgba(0, 0, 0, 0.25);
}
.list__text{
    font-family: ComicReg;
    font-size: 15px;
}

.list-true .list__text{
    font-family: ComicLI;
    font-size: 15px;
}
.btn{
    font-family: PopMedium;
    font-size: 15px;
    border:none;
    background-color: transparent;
    padding: 0px 5px;
    margin: 2px;
    border-radius: 3px;
    color:white;
}
.btn__delete{
    background-color:#fa6363;
}
.btn__update{
    background-color:#636dfa;
}
.btn__save{
    background-color:#636dfa;
}
.btn__remove{
    background-color:#fa6363;
}
.btn__add{
    border:2px solid teal;
    color:teal;
}
.btn__disabled{
    background-color: grey;
}
.btn__add__disabled{
    border:2px solid rgba(0, 0, 0, 0.25);
    color:rgba(0, 0, 0, 0.25);
}
.list-draggind{
    width: 16px;
    height: 16px;
    margin-right:4px;
    display: grid;
    grid-template-columns: repeat(2,50%);
    grid-template-rows: repeat(2,50%);
    justify-content: center;
    animation: circle 3s infinite ease-out;
    transition-delay: 2s;
}
.list-draggind span{
    display: flex;
    width: 1px;
    height: 1px;
    border-radius: 100%;
    background-color: tomato;
}

@keyframes circle {
    from { transform: rotate(0deg) }
    to   {  transform: rotate(360deg) }
}